export default {
    data(){
        return {
            parent:null
        }
    },
    methods:{
        // ,
        findParent(name){
            let parent = this.$parent;
            while(parent){
                if(parent.$options._componentTag===name){
                    this.parent = parent;
                    return true;
                }
                parent = parent.$parent;
            }
            return false;
        }
    }
}