
import Vue from "vue"; //  Vue  Vue.extend() 
import Dialog from './index.vue'

let constructor = Vue.extend(Dialog); // 
let instance;

let dialogInstance = {}
dialogInstance = function (options = {}) {
    return new Promise((resolve, reject) => {
        instance = new constructor({
            propsData: options
        }).$mount(); // 
        document.body.appendChild(instance.$el); //  body 
        resolve();
    })
};
Vue.prototype.$dialog = dialogInstance;

export default {
    Dialog, dialogInstance
};
